<template>
    <v-card flat color="transparent" width="400px">
        <v-card-title>
            <v-layout>
                <v-flex>
                    <v-card 
                        elevation="5"
                        style="border-radius: 20px" 
                        color="white" 
                        width="100px" 
                        height="100px"
                        >
                        <v-img :src="logo(app.logo)"/>
                    </v-card>
                </v-flex>
                <v-flex>
                    <div>{{app.name}}</div>
                    <div>
                        <v-chip pill color="blue" dark>{{app.price}}</v-chip>
                    </div>
                    <template v-for="(platform,i) in app.platform">
                        <v-btn icon :key="i">
                        <v-icon>{{platform.icon}}</v-icon>
                        </v-btn>
                    </template>
                </v-flex>
            </v-layout>
        </v-card-title>
        <v-card-text>
            <div>{{app.description}}</div>
            <v-list-item-content>
                <v-list-item-subtitle>{{app.link}}</v-list-item-subtitle>
            </v-list-item-content>
            <div class="title mt-2">Technologies Used</div>
            <template v-for="(tech, i) in app.technologies">
                <v-btn icon :key="i">
                <v-icon>{{tech.icon}}</v-icon>
                </v-btn>
            </template>
            <div class="title mt-2">Code</div>
            <template v-for="(code, i) in app.code">
                <v-btn icon :key="i">
                <v-icon>{{code.icon}}</v-icon>
                </v-btn>
            </template>
        </v-card-text>
    </v-card>
</template>

<script>
export default {
    props: {
        app: {
            type: Object,
            default: function(){
                return {};
            }
        }
    },
    methods: {
      logo(name){
        return require("../assets/images/"+name)
      }
    }
}

</script>